<template>
    <div class="video_detail">
      <div class="header">
          <div class="back"  @click.stop="back()">
              <img src="../../assets/image/联合42.png" alt="">
          </div>
      </div>
      <div class='video_container'>
        <div class='video_container_top'>
          <div class='video_title'>{{name}}</div>
          <div class='video_word'>
            <div class='video_visit'>浏览 {{viewNum}}</div>
            <div @click.stop='changeLike()'>
              <img src="../../assets/image/视频未喜欢.png" alt="" v-if='favo == false'>
              <img src="../../assets/image/视频喜欢.png" alt=""  v-if='favo == true'>
            </div>

          </div>
        </div>
        <div class='video_player'>
          <div class="player">
              <video-player  class="vjs-custom-skin video-player"
                              ref="videoPlayer"
                              :options="playerOptions"
                              :playsinline="true"
                              :webkit-playsinline="true"
                              :events="events"
                              @pause="onpause($event)"
                              @play="onPlayerPlay($event)"
                              @fullscreenchange="onPlayerFullScreenchange($event)"
                              @timeupdate="onPlayerTimeupdate($event)"
                              >
              </video-player>
          </div>
        </div>
        <div class='video_introduction'>
          <div class='introduction_top'>
            简介
          </div>
          <div class='introduction_contianer'>
            <div :class="showTotal ? 'total-introduce' : 'detailed-introduce'">
              <div class="intro-content" :title="briefIntro" ref="desc">
                <span class="merchant-desc" v-if="briefIntro">
                  {{briefIntro}}
                </span>
                <div class="unfold" @click="showTotalIntro" v-if="showExchangeButton">
                  <p>{{exchangeButton ? '展开' : '收起'}}</p>
                </div>
              </div>
            </div>
             <!-- {{briefIntro}} -->
          </div>
        </div>
      </div>
      <!-- 加载页面 -->
      <Loading :dialogVisible="verDialogVisible"></Loading>
    </div>
</template>

<script>
// custom skin css
//   import '../src/custom-theme.css'
import API from '@/api/api'
import Loading from './../components/loading'
// import 'videojs-flash'

export default {
  components: { Loading },
  data () {
    return {
      events: ['fullscreenchange'],
      ifFullscreen: false,
      // videojs options
      favo: false, // 是否喜欢
      islike: '0',
      viewNum: 0, // 浏览量
      id: '',
      videoList: '',
      playerOptions: {}, // 视频组件
      name: '',
      showTotal: true,
      // 显示展开还是收起
      exchangeButton: true,
      // 是否显示展开收起按钮
      showExchangeButton: false,
      verDialogVisible: true, // 加载页
      rem: '',
      briefIntro: '', // 是否展示所有文本内容
      addTime: 0, // 时间增长
      spendTime: 0 // 时间改变时获取的当前播放的时间
    }
  },
  created () {
    this.id = this.$route.params.id
    this.specialCode = this.$route.params.specialCode
    this.findVideoDetilsById()
  },
  mounted () {
    // 监听返回键
    document.addEventListener('backbutton', this.onBackKeyDown, false)
  },
  // 消除返回键监听
  beforeDestroy () {
    document.removeEventListener('backbutton', this.onBackKeyDown, false)
  },
  computed: {
    player () {
      return this.$refs.videoPlayer.player
    }
  },
  methods: {
    onpause (player) {
      this.addTime--
    },
    onPlayerPlay (player) {
      this.addTime++
    },
    // 全屏按钮
    onPlayerFullScreenchange (player) {
      this.ifFullscreen = player.isFullscreen()
    },
    onPlayerTimeupdate (player) {
      this.spendTime = parseInt(player.currentTime())
    },
    // 返回键
    onBackKeyDown () {
      this.back()
    },
    back () {
      this.$router.back()
      // this.addFavorite()
    },
    // 喜欢接口
    addFavorite () {
      if (this.favo) {
        this.islike = '1'
      } else {
        this.islike = '0'
      }
      API.post('addFavorite', {
        trainMateId: this.id,
        favo: this.islike
      }, 'json', (data, err) => {
        if (data.success) {
          // console.log(data)
        }
      })
    },
    changeLike () {
      this.favo = !this.favo
      this.addFavorite ()
    },
    findVideoDetilsById () {
      API.post('findVideoDetilsById', {
        id: this.id
      }, 'json', (data, err) => {
        if (data.success) {
          this.videoList = data.data
          this.name = this.videoList.name
          this.briefIntro = this.videoList.briefIntro
          this.viewNum = this.videoList.viewNum
          if (this.videoList.favo === '0') {
            this.favo = false
          } else {
            this.favo = true
          }
          this.playerOptions = {
            hls: true,
            autoplay: false, // 自动播放
            controls: true, // 控制条
            muted: false, // 消除任何音频
            loop: false, // 导致视频一结束就重新开始。
            language: 'zh-CN',
            fluid: true, // 按比例缩放
            preload: 'auto', // 视频预加载
            //playbackRates: [0.7, 1.0, 1.5, 2.0], // 播放速度
            aspectRatio: '16:9',
            sources: [{
              type: 'application/x-mpegURL',
              // type: "video/mp4",
              // src: "http://vjs.zencdn.net/v/oceans.mp4",
              // src:'http://192.168.2.168:19002/hls/2019-09-03-17:38:25-025/index.m3u8'
              // src:'http://192.168.2.168:8089/hls/yinxiangjie/index.m3u8'
              // src:'http://ivi.bupt.edu.cn/hls/cctv1hd.m3u8'
              src: this.videoList.url
            }],
            poster: this.videoList.imageUrl,
            // poster:'http://152.136.158.153:8080/pxks/dl/3d7b28eb971f176e9a0ae2e8f9d5754c25a8413e537bff1f754c1fe31e1a6589',
            notSupportedMessage: '此视频暂无法播放，请稍后再试', // 允许覆盖Video.js无法播放媒体源时显示的默认信息。
            controlBar: {
              timeDivider: true,
              durationDisplay: true,
              remainingTimeDisplay: false,
              // progressControl:false,
              fullscreenToggle: true // 全屏按钮
            }
          }
          this.verDialogVisible = false
        } else {
          console.log(data.msg)
        }
      })
    },
    showTotalIntro () {
      this.showTotal = !this.showTotal
      this.exchangeButton = !this.exchangeButton
    },
    getRem () {
      const defaultRem = 16
      const winWidth = window.innerWidth
      const rem = winWidth / 375 * defaultRem
      return rem
    },
    addSelfCredits () {
      // API.post('addSelfCredits', {
      //   specialCode:this.specialCode,
      //   creditsId:'learnTime'
      // }, 'json',(data,err)=>{
      //   if(data.success){
      //     console.log(data)
      //   }
      // })
    }

  },
  watch: {
    spendTime (val, oldval) {
      // this.add();
      this.addTime++
    },
    addTime (val) {
      if (val % 300 === 0) {
        this.addSelfCredits()
      }
    },
    // 全屏按钮
    ifFullscreen (val, oldval) {
      if (val) {
        screen.orientation.lock('landscape')
        // eslint-disable-next-line no-undef
        StatusBar.hide()
        return
      }
      screen.orientation.lock('portrait')
      // eslint-disable-next-line no-undef
      StatusBar.show()
    },
    // 展开和收起判断
    briefIntro () {
      this.$nextTick(() => {
        // 判断介绍是否超过四行
        const rem = parseFloat(this.getRem())
        // console.log('watch 中的rem', rem);
        if (!this.$refs.desc) {
          return
        }
        const descHeight = window.getComputedStyle(this.$refs.desc).height.replace('px', '')
        if (descHeight > 5.25 * rem) {
          // console.log('超过了四行');
          // 显示展开收起按钮
          this.showExchangeButton = true
          this.exchangeButton = true
          // 不是显示所有
          this.showTotal = false
        } else {
          // 不显示展开收起按钮
          this.showExchangeButton = false
          // 没有超过四行就显示所有
          this.showTotal = true
        }
      })
    }
  }
}
</script>
<style scoped>
    .video_detail{
        width:100%;
        height:100vh;
    }
    .video_detail .header{
        width:100%;
        background:#fff;
        font-size:0.36rem;
        font-family: PingFang-SC-Medium;
        position: fixed;
        top:0;
        height: 0.94rem;
        display: flex;
        justify-content: center;
        z-index: 99;
    }
    .video_detail .back{
        width:0.89rem;
        height:0.94rem;
        position: fixed;
        top:0;
        left:0;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 100;
    }
    .video_detail .back img{
        width:0.19rem;
        height:0.34rem;
    }
    .video_detail .video_container{
      margin-top:0.94rem;
      width:100%;
      height:calc(100vh - 0.94rem);
      overflow-y: auto;
      overflow-x:hidden;
    }
    .video_detail .video_container .video_container_top{
      /* height:1.13rem; */
      width:100%;
      padding:0 0.35rem;
      border-bottom: 0.012rem solid #EBEFF5;
    }
    .video_container .video_container_top .video_title{
      width:100%;
      font-size:0.4rem;
      color:#384150;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      /* height:0.75rem; */
      word-break: break-word;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      line-height: 0.57rem;
      /* width: calc(100vw - 4.3rem); */
      /*! autoprefixer: off */
        /* autoprefixer: on */
    }
    .video_container .video_container_top .video_word{
      width:100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      height:0.53rem;
      margin-top:0.03rem;
      margin-bottom: 0.22rem;
    }
    .video_container .video_container_top .video_word .video_visit{
      font-size:0.26rem;
      color:#9FA7AF;
    }
    .video_container .video_container_top .video_word img{
      width:0.53rem;
      height:0.53rem;
      display: block;
    }
    .video_container .video_player{
      width:100%;
      padding:0.3rem 0.35rem;
      border-bottom: 0.012rem solid #EBEFF5;
    }
    .video_container .video_player .player{
      width:100%;
    }
    .video_container .video_introduction{
      width:100%;
      padding:0 0.35rem;
    }
    .video_introduction .introduction_top{
      width:100%;
      padding-top:0.31rem;
      font-size:0.36rem;
      color:#2E2D30;
    }
    .video_introduction .introduction_contianer{
      margin-top:0.2rem;
      width:100%;
      font-size:0.32rem;
      color:#384150;
      /* line-height: 0.5rem; */
    }

</style>
<style lang="less" scoped rel="stylesheet/less">
  .total-introduce {
    word-wrap: break-word;
    word-break: normal;
    height: auto;
    overflow: hidden;
    font-size: 0.32rem;
    color: #434343;
    // margin: 10px;
    .intro-content {

      .merchant-desc {
        width: 100%;
        line-height: 0.5rem;

      }
    }
    .unfold {
      display: block;
      z-index: 11;
      float: right;
      width: 0.8rem;
      height: 0.5rem;
      p {
        margin: 0;
        line-height:0.5rem;
        color: #7fbe87;
      }
    }
  }
  .detailed-introduce {
    font-size: 0.32rem;
    color: #434343;
    position: relative;
    overflow: hidden;
    // margin: 10px;
    .intro-content {
      // 最大高度设为4倍的行间距
      max-height: 2rem;
      line-height: 0.5rem;
      word-wrap: break-word;
      /*强制打散字符*/
      word-break: break-all;
      word-break: normal;
      background: #ffffff;
      /*同背景色*/
      color: #ffffff;
      overflow: hidden;
      .merchant-desc {
        width: 100%;
        line-height: 0.5rem;
      }
      &:after,
      // 这是展开前实际显示的内容
      &:before {
        content: attr(title);
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        color: #434343
        // overflow: hidden;
      }
      // 把最后最后一行自身的上面三行遮住
      &:before {
        display: block;
        overflow: hidden;
        z-index: 1;
        max-height: 1.5rem;
        background: #ffffff;
      }
      &:after {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        overflow: hidden;
        height: 2rem;
        /*截取行数*/
        -webkit-line-clamp: 4;
        text-overflow: ellipsis;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        /*行首缩进字符数，值为[(截取行数-1)*尾部留空字符数]*/
        text-indent: -12em;
        /*尾部留空字符数*/
        padding-right: 4em;
      }
      .unfold {
        z-index: 11;
        width: 0.8rem;
        height: 0.5rem;
        outline: 0;
        position: absolute;
        right: 0;
        bottom: 0;
        p {
          margin: 0;
          line-height: 0.5rem;
          color: #7fbe87;
        }
      }
    }
  }
</style>



